.main{
  padding: 3.75em 0 4.38em;
  position: relative;
  &__img,
  &__img2{
    position: absolute;
    top: 0;
  }
  &__img{
    left: 0;
    width: 28.9%;
  }
  &__img2{
    right: 0;
    width: 33.3%;
  }
}

.info-block{
  text-align: center;
  max-width: 33em;
  width: 100%;
  margin: 0 auto;
  &__split{
    display: block;
    margin: 1.875em auto;
  }
  @media screen and (min-width: 993px){
    br{
      display: none;
    }
  }
}

.main-form{
  background-color: #fff;
  position: relative;
  margin: 5.56em auto 0;
  width: 67.875em;
  max-width: 100%;
  border: 1px solid #DCDCDC;
  border-radius: 1em;
  padding: 3.25em 2.5em 3em;
  &__top{
    text-align: center;
    @include grid100gap(1em);
    &:after{
      content: '';
      display: block;
      width: 10.375em;
      height: 1px;
      background: #DCDCDC;
      margin: 0 auto;
    }
    .text22{
      color: #363636;
    }
  }
  &__img{
    display: none;
  }
  &__call{
    margin-top: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    .phone-link{
      margin-left: .5em;
    }
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 16em 16em calc(100% - 50em) 15em;
    align-items: center;
    gap: 1em;
  }
  &__btn{
    padding: 0;
    width: 100%;
    font-weight: 700;
    span{
      font-size: 1.375em;
    }
  }
}

.consult{
  background-image: url(../img/consult.jpg);
  background-size: cover;
  background-position: center;
  padding: 8.35em 0;
}

.consult-form,
.main-form{
  &._success .thank{
    opacity: 1;
    pointer-events: unset;
  }
}

.consult-form{
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: 31.81em calc(100% - 31.81em);
  align-items: center;
  border-radius: 1em;
  background: #FFF;
  box-shadow: 0px 4px 44px 0px rgba(0, 0, 0, 0.07);
  &__info{
    padding: 3.5em 5.5em 4.375em 3.5em;
    border-right: 1px solid #E7E7E7;
  }
  .main-form__top{
    text-align: left;
    &:after{
      margin-left: 0;
    }
  }
  .main-form__img{
    display: block;
    width: 4.75em;
  }
  .main-form__call{
    @media screen and (min-width: 993px){
      flex-direction: column;
      align-items: flex-start;
      .phone-link{
        margin: .5em 0 0;
      }
    }
  }
  &__fields{
    @include grid100gap(1em);
    padding: 2.875em;
  }
  &__btn{
    width: 100%;
    font-weight: 600;
    span{
      font-size: 1.125em;
    }
  }
}

.thank{
  padding: 1em;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: inherit;
  border-radius: inherit;
  z-index: 3;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  @include center;
  &__icon{
    width: 10.875em;
    flex-shrink: 0;
    margin-right: 3.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(1em);
  }
}

.furniture{
  padding: 2.625em 0 4.44em;
  &__grid{
    display: grid;
    grid-template-columns: 34.375em calc(100% - 34.375em);
  }
  &__content{
    min-height: 50em;
    width: calc(100% + 13.125em);
    margin-left: -13.125em;
    padding: 4.94em 8em 3em 13.125em;
    background-size: cover;
    background-position: center;
    background-image: url(../img/furniture.jpg);
    border-radius: 0.5em;
    color: #fff;
    &:before{
      //background: linear-gradient(180deg, rgba(0, 0, 0, 0.58) 0%, rgba(0, 0, 0, 0.00) 100%);
    }
  }
  &__info{
    position: relative;
    z-index: 2;
    @include grid100gap(1.5em);
  }
  &__items{
    padding-left: 4.25em;
  }
}

.furniture-item{
  max-width: 29.625em;
  @include grid100gap(1.5em);
  padding: 2.9em 0;
  position: relative;
  @media screen and (min-width: 993px){
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      padding-bottom: 0;
      &:before,
      &:after{
        display: none;
      }
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      bottom: 0;
      transform: translateY(50%);
    }
    &:before{
      width: calc(100% + 8.81em);
      height: 1px;
      background: #D9D9D9;
      right: 0;
    }
    &:after{
      width: 0.8125em;
      height: 0.8125em;
      background-color: #fff;
      border-radius: 50%;
      left: -8.81em;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: 1.9375em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  .text16{
    letter-spacing: 0.005em;
    color: #363636;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 6.125em 0 1.56em;
    &__img{
      display: none;
    }
    &__img2{
      top: 3.56em;
      width: 23.3125em;
    }
    .info-block{
      max-width: 25em;
    }
  }

  .info-block{
    text-align: left;
    margin-left: 0;
    &__split{
      margin: 1.125em 0;
    }
  }

  .main-form{
    margin-top: 9.2em;
    padding: 3.25em 2.5em 1.81em;
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1.5em 1em;
    }
    &__btn{
      height: 3.9375em;
      width: 14.75em;
      max-width: 100%;
      margin-left: auto;
    }
  }

  .consult{
    padding: 2.9em 0;
  }

  .consult-form{
    grid-template-columns: 100%;
    &__info{
      border-right: none;
      border-bottom: 1px solid #E7E7E7;
      padding: 3.5em 3.56em 2.31em;
    }
    .main-form__img{
      width: 3.2em;
    }
    .main-form__call{
      justify-content: unset;
      @media screen and (min-width: 577px){
        .phone-link{
          margin-left: 1.12em;
        }
      }
    }
    &__fields{
      padding: 2.31em 3.56em 3.88em;
    }
  }

  .thank{
    flex-direction: column;
    text-align: center;
    &__icon{
      margin: 0 0 3.875em;
    }
  }

  .furniture{
    padding: 1.5em 0 4.8em;
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
    }
    &__content{
      border-radius: 0;
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      min-height: unset;
      padding: 3.5em 1.25em;
    }
    &__info{
      max-width: 24.5em;
    }
    &__items{
      padding: 0;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 3.44em 2.5em;
    }
  }

  .furniture-item{
    padding: 0;
  }
}


@media screen and (max-width: 576px){
  .main{
    overflow: hidden;
    padding: 8.69em 0 1.625em;
    &__img2{
      top: -2.3725em;
      width: 12.125em;
    }
    .info-block{
      .text53{
        max-width: 80%;
      }
    }
  }

  .main-form{
    margin-top: 1.375em;
    padding: 1.625em 0 1.16em;
    &__top{
      text-align: left;
      padding: 0 1.875em;
      &:after{
        margin-left: 0;
      }
    }
    &__img{
      display: block;
      width: 3.2em;
    }
    &__call{
      padding: 0 1.875em;
      flex-direction: column;
      align-items: flex-start;
      .phone-link{
        margin: .75em 0 0;
        span{
          font-size: 1em;
        }
        svg{
          width: 1.125em;
        }
      }
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: .875em;
      padding: 1.25em 1.25em 0;
      border-top: 1px solid #E7E7E7;
    }
    &__btn{
      height: 3.25em;
      font-weight: 600;
      span{
        font-size: .875em;
      }
    }
  }

  .consult{
    padding: 1.5em 0;
  }

  .consult-form{
    .main-form__call,
    .main-form__top{
      padding: 0;
    }
    &__info{
      padding: 1.62em 1.875em 1.38em;
    }
    &__fields{
      padding: 1.28em 1.06em 1.16em;
      gap: .875em;
    }
  }

  .thank{
    &__icon{
      width: 3.2em;
      margin-bottom: 1em;
    }
    &__content{
      gap: .5em;
    }
  }

  .furniture {
    padding: 1.3em 0 2.19em;
    &__content{
      padding: 1.81em 1.25em 2.31em;
    }
    &__grid{
      gap: 1.69em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__info{
      gap: 1.125em;
    }
  }

  .furniture-item{
    gap: .875em;
    .text20{
      font-size: 1em;
    }
    .text16{
      font-size: .875em;
    }
  }
}
