.footer{
  padding: 2.75em 0 1.5em;
  background: #353535;
  color: #FFF;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    display: flex;
    align-items: flex-start;
  }
  &__logo{
    display: block;
    width: 9.125em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    max-width: 26.0625em;
    @include grid100gap(.875em);
  }
  &__c{
    color: #AFAFAF;
    font-size: .875em;
    line-height: 221%;
    font-weight: 400;
  }
  &__text{
    font-size: .75em;
    line-height: 140%;
    color: #838383;
  }
  &__bottom{
    margin-top: 1.625em;
    padding-top: 1.2em;
    display: flex;
    align-items: center;
    border-top: 1px solid #494949;
    &:before{
      content: '';
      width: 9.125em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    margin-right: 1.5em;
    text-decoration: underline;
    span{
      font-size: .75em;
      line-height: 140%;
    }
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  .text22,
  .text14{
    line-height: 221%;
  }
  &__address{
    margin-top: .5em;
  }
}

.footer-contacts2{
  display: flex;
  align-items: center;
  margin-left: auto;
  .socials{
    margin-right: 2.81em;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.375em;
    flex-shrink: 0;
    @include lastM(0 1em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path:not(._not-fill){
        fill: $primary;
      }
    }
  }
}

.made-by{
  font-size: .75em;
  line-height: 140%;
  a{
    display: block;
    line-height: inherit;
    font-weight: 500;
    text-decoration: underline;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.06em 0 1.44em;
    &__left{
      flex-direction: column;
      max-width: 25.75em;
    }
    &__logo{
      margin: 0 0 .75em;
      width: 6em;
    }
    &__info{
      gap: .25em;
    }
    &__bottom{
      margin-top: 2.12em;
      &:before{
        display: none;
      }
    }
  }

  .footer-contacts{
    max-width: 15.375em;
    margin-top: -1.4em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 2em;
    &__top{
      display: block;
    }
    &__bottom{
      padding-top: 1.81em;
      margin-top: 1.5em;
      display: block;
    }
  }

  .footer-contacts{
    margin-top: 1.5em;
    .text22{
      font-size: 1.125em;
    }
  }

  .footer-contacts2{
    margin-top: 1.69em;
    display: block;
    .socials{
      margin: 0 0 1.56em;
    }
  }
}
