.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    z-index: 100;
    text-align: center;
    overflow-y: auto;
    padding: 1em;
    opacity: 0;
    pointer-events: none;
    @media screen and (min-width: 992px){
        @media screen and (max-width: 1700px){
            font-size: 1.25em;
        }
    }
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &::after {
        display: inline-block;
        vertical-align: middle;
        width: 0;
        height: 100%;
        content: '';
    }
    &__bg{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: $fullHeight;
        background: rgba(60, 58, 58, 0.70);
    }
    &__dialog{
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        width: 63.4375em;
        max-width: 100%;
        position: relative;
        z-index: 2;
        background-color: #fff;
        border-radius: 0.9375em;
    }
    &__close{
        z-index: 5;
        position: absolute;
        top: .75em;
        right: .75em;
        width: 1.875em;
        background-color: transparent;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog{
            display: block;
        }
    }

}


@media screen and (max-width: 576px){
    .modal{

