.solution{
  padding-bottom: 4.38em;
  border-bottom: 1px solid #E0E0E0;
}

.solution-banner{
  background-image: url(../img/solution.webp);
  background-size: cover;
  background-position: center;
  padding: 5.19em 4.37em 4.7em;
  border-radius: 1.25em;
  width: calc(100% + 23.75em);
  margin-left: -11.875em;
  color: #fff;
  &__inner{
    width: 65.875em;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    text-align: center;
    @include grid100gap(.875em);
  }
  &__title{
    font-size: 3.625em;
    line-height: 111%;
    font-weight: 600;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 150%;
    letter-spacing: 0.025em;
  }
}

.why{
  margin-top: 3.2em;
  display: grid;
  grid-template-columns: calc(100% - 30em) 26.875em;
  gap: 2.44em;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.69em));
    gap: 2em 5.38em;
    margin-top: 1.75em;
    max-width: 33em;
  }
  &__sidebar{
    padding-left: 4em;
    border-left: 1px solid #E0E0E0;
  }
}

.why-item{
  color: $dark;
  &__icon{
    width: 3.4375em;
    height: 3.4375em;
    display: flex;
    align-items: center;
    margin-bottom: 1.125em;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.why-call{
  @include grid100gap(1.5em);
  .phone-link{
    justify-content: unset;
  }
  &__phone{
    display: flex;
    margin-top: -.5em;
  }
  &__btn{
    width: 20.875em;
    max-width: 100%;
  }
  .text26{
    span{
      color: $primary;
    }
  }
  &__img{
    display: block;
    width: 4.75em;
  }
}

.what{
  overflow: hidden;
  padding: 2.38em 0 2.1em;
  &__top{
    @include grid100gap(1em);
  }
}

.what-slider{
  margin: 3.25em auto 0;
  max-width: 93.3125em;
  width: 100%;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4,calc(25% - .71em * 3 / 4));
        gap: .71em;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.what-card{
  position: relative;
  color: #fff;
  &__img{
    border-radius: .5em;
    @include imgCover(156%);
    &:before{
      z-index: 3;
    }
  }
  &__content{
    @include centerAbsolute;
    width: calc(100% - 2em);
    height: calc(100% - 2em);
    border-radius: 0.3125em;
    border: 2px solid transparent;
    @include center;
    flex-direction: column;
    transition: all .3s;
    &:before{
      content: '';
      height: 2.25em;
      width: 100%;
      display: block;
      margin-bottom: 1.5em;
    }
  }
  &__title{
    font-size: 2.125em;
    line-height: 111%;
    font-weight: 600;
    letter-spacing: 0.0425em;
  }
  &__btn{
    opacity: 0;
    margin-top: 1.5em;
    @include center;
    height: 2.25em;
    padding: 0 1.0625em;
    color: #FFF;
    border-radius: 3.125em;
    border: 1px solid #FFF;
    font-weight: 600;
    span{
      font-size: 1em;
      line-height: 111%;
      letter-spacing: 0.02em;
    }
    &:hover{
      background-color: #fff;
      color: $primary;
    }
  }
  &:hover{
    .what-card__content{
      border-color: #fff;
    }
    .what-card__btn{
      opacity: 1;
    }
  }
}

.reviews{
  overflow: hidden;
  padding: 2.7em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(1em);
  }
  &__info2{
    display: flex;
  }
  &__logo{
    width: 7.3125em;
    flex-shrink: 0;
    margin-right: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__rating{
    .text18{
      line-height: 201%;
    }
    .rating{
      margin-top: .25em;
    }
  }
  &__link{
    margin-top: 1em;
    height: 2em;
    width: 7.25em;
    padding: 0;
    border-radius: 3.125em;
    color: $primary;
    border-color: $primary;
    span{
      font-size: 0.875em;
      line-height: 120%;
    }
  }
}

.process{
  padding: 2.1em 0 2.7em;
  &__top{
    max-width: 34.125em;
    @include grid100gap(1.5em);
    .text20{
      letter-spacing: 0.025em;
      color: #363636;
    }
  }
  &__items{
    margin-top: 4.87em;
    display: flex;
    align-items: stretch;
    width: calc(100% + .625em);
  }
}

.process-item{
  transition: all .1s;
  width: calc(100% - 47.125em);
  height: 24.5em;
  margin-right: .625em;
  padding: 1.25em 2.5em 1.56em;
  border-radius: 0.9375em;
  background: #F8F8F8;
  position: relative;
  &:not(._active){
    @media screen and (min-width: 993px){
      width: 11em;
      padding-left: 1.25em;
      padding-right: 1.25em;
      .process-item__bottom{
        opacity: 0;
        pointer-events: none;
        height: 1em;
        overflow: hidden;
      }
    }
  }
  &._active{
    .process-item__num{
      color: $primary;
    }
    .process-item__arrow{
      opacity: 0;
      pointer-events: none;
    }
  }
  &__top{
    @include grid100gap(1.5em);
  }
  &__bottom{
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: 1px solid #D9D9D9;
    position: relative;
    z-index: 2;
    .text16{
      @include truncate(5);
    }
  }
  &__btn{
    margin-top: 1.5em;
    height: 3.375em;
    border-radius: 0.5em;
    padding: 0;
    width: 100%;
    font-weight: 500;
    span{
      font-size: 1em;
    }
  }
  &__arrow{
    transition: all .1s;
    position: absolute;
    width: calc(100% - 2.5em);
    height: 5.6875em;
    left: 1.25em;
    bottom: 1.875em;
    @include center;
    img{
      width: 1em;
    }
  }
}

.projects{
  padding: 4.81em 0 1.4em;
  overflow: hidden;
  &__top{
    @include grid100gap(1.5em);
  }
}

.projects-slider{
  margin-top: 4.44em;
  width: calc(100% + 8.875em);
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.875em;
      height: auto;
      @media screen and (min-width: 993px){
        &-active~.swiper-slide{
          .project-card{
            &__img{
              margin-left: -29.5em;
            }
          }
        }
      }
    }
    &-nav{
      display: flex;
      align-items: center;
      position: absolute;
      left: 10.125em;
      bottom: 6.44em;
    }
    &-button{
      position: static;
      margin: 0 1em 0 0;
    }
  }
}

.project-card{
  height: 100%;
  display: grid;
  grid-template-columns: 28em calc(100% - 29.5em);
  gap: 1.5em;
  &__content{
    padding-top: 2.7em;
    padding-bottom: 6.44em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    max-width: 26.1875em;
    @include grid100gap(1.75em);
    .text32{
      color: $primary;
    }
    .text18{
      color: $dark;
    }
  }
  &__split{
    width: 7.9375em;
  }
  &__list{
    margin-top: 1.75em;
    @include grid100gap(.875em);
    li{
      display: flex;
      align-items: center;
      color: $dark;
      &:before{
        content: '';
        width: 0.75em;
        height: 0.09375em;
        flex-shrink: 0;
        background: $black;
        margin-right: 1em;
      }
      span{
        font-size: .875em;
        line-height: 140%;
      }
    }
  }
  &__nav{
    display: flex;
    padding-top: 1.75em;
    margin-top: auto;
  }
  &__link{
    width: 9.125em;
    height: 3.375em;
    border-radius: 3.75em;
  }
  &__img{
    transition: all .3s;
    img{
      width: 100%;
      height: 36.375em;
      object-fit: cover;
      display: block;
      border-radius: .5em;
    }
  }
}

.projects,
.reviews{
  .swiper-scrollbar{
    display: none;
  }
}

.reviews-slider{
  margin-top: 6.13em;
  padding: 0 6.125em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 1.875em;
      height: auto;
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.review-card{
  min-height: 19em;
  padding: 2.125em 1.875em;
  border-radius: 0.625em;
  border: 1px solid #D3D3D3;
  &:not(._all){
    .review-card__text{
      @include truncate(4);
    }
    .review-card__link ._view2{
      display: none;
    }
  }
  &._all{
    .review-card__link ._view1{
      display: none;
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 2.5em;
    height: 2.5em;
    margin-right: 1.25em;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 50%;
    }
  }
  &__info{
    @include grid100gap(.19em);
  }
  &__date{
    font-size: 0.75em;
    line-height: 140%;
    letter-spacing: 0.0075em;
    color: #363636;
  }
  &__site{
    align-self: flex-start;
    padding: .31em;
    margin-left: auto;
    img{
      width: 1.1875em;
      display: block;
    }
  }
  &__nav,
  &__text,
  .rating{
    margin-top: 1.125em;
  }
  &__nav{
    display: flex;
  }
  &__link{
    color: $primary;
    font-weight: 500;
    &:hover{
      color: $primaryHover;
    }
  }
}


@media screen and (max-width: 992px){
  .solution{
    padding-bottom: 2em;
    border: none;
  }

  .solution-banner{
    padding-top: 4.31em;
    padding-bottom: 3.19em;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    border-radius: 0;
    &__title{
      font-size: 2.375em;
      max-width: 80%;
      margin: 0 auto;
    }
    &__desc{
      font-size: 1.125em;
    }
  }

  .why{
    margin-top: 2.56em;
    grid-template-columns: 100%;
    gap: 2.5em;
    &__sidebar{
      padding: 2.5em 0 0;
      border-left: none;
      border-top: 1px solid #E0E0E0;
    }
    &__items{
      max-width: 100%;
    }
  }

  .why-call{
    &__btn{
      width: 100%;
    }
  }

  .what{
    padding: 2em 0 1.4em;
  }

  .what-slider{
    margin-top: 2.875em;
    .swiper{
      &-slide{
        padding: 0 .36em;
        width: 23em;
      }
    }
  }

  .process{
    padding: 1.4em 0 1.9em;
    &__top{
      max-width: 42em;
    }
    &__items{
      margin-top: 3.5em;
      width: 100%;
      @include grid100gap(.625em);
    }
  }

  .process-item{
    width: 100%;
    height: unset;
    margin: 0;
    &:not(._active){
      .process-item{
        &__top{
          display: flex;
          align-items: center;
          gap: unset;
          .text50{
            margin-right: .5em;
          }
        }
        &__bottom{
          display: none;
        }
      }
    }
    &__arrow{
      width: 6em;
      height: 100%;
      left: unset;
      right: 0;
      top: 0;
      img{
        transform: rotate(90deg);
      }
    }
  }

  .projects{
    padding: 2em 0;
  }

  .projects-slider{
    margin-top: 1.125em;
    width: calc(100% + 1.25em);
    .swiper{
      &-slide{
        padding-right: 1.25em;
      }
      &-nav{
        bottom: 0;
      }
    }
  }

  .project-card{
    grid-template-columns: 100%;
    gap: 2.5em;
    &__img{
      order: -1;
      border-radius: .5em;
      @include imgCover(62%);
    }
    &__content{
      padding: 0;
    }
    &__top{
      max-width: 100%;
      .text32{
        br{
          display: none;
        }
      }
    }
  }

  .reviews{
    padding: 1.9em 0 1.5em;
  }

  .reviews-slider{
    margin-top: 3em;
    padding: 0 1.625em;
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .solution {
    padding-bottom: 1.1em;
  }

  .solution-banner{
    padding: 1.81em 1.56em 2.81em;
    &__title{
      font-size: 2.125em;
      max-width: 100%;
    }
    &__desc{
      font-size: 1em;
    }
  }

  .why{
    gap: 2em;
    &__items{
      grid-template-columns: 100%;
      gap: 2.13em;
    }
  }

  .why-call{
    &__btn{
      height: 4.1875em;
      span{
        font-size: 1.125em;
      }
    }
  }

  .what{
    padding: 1.35em 0 .72em;
  }

  .what-slider{
    margin-top: 2.2em;
    .swiper{
      &-slide{
        padding: 0 .22em;
        width: 14.8em;
      }
    }
  }

  .what-card{
    &__title{
      font-size: 1.625em;
    }
  }

  .process{
    &__items{
      margin-top: 1.75em;
    }
  }

  .process-item{
    padding: 1.125em;
    &__top{
      padding-right: 3.5em;
      gap: 1.125em;
      .text50{
        font-size: 2.75em;
        margin-right: 1.8em;
      }
    }
    &__arrow{
      width: 2.9375em;
      right: 1.25em;
      img{
        width: .75em;
      }
    }
    &__bottom{
      padding-top: 1.125em;
      margin-top: 1.125em;
      .text16{
        font-size: .875em;
      }
    }
    &__btn{
      margin-top: 1.125em;
    }
  }

  .projects{
    padding: 1.2em 0 1.4em;
  }

  .projects-slider{
    margin-top: 1.31em;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    .swiper{
      &-slide{
        padding-right: 0;
      }
      &-nav{
        left: 11.375em;
        bottom: 2em;
      }
    }
  }

  .project-card{
    gap: 0;
    &__img{
      padding-bottom: 91.25%;
      border-radius: 0;
    }
    &__content{
      padding: 1.56em 1.25em 2em;
    }
    &__top{
      gap: 1.125em;
      .text32{
        font-size: 1.5em;
      }
    }
    &__list{
      margin-top: 1.125em;
    }
    &__nav{
      padding-top: 1.125em;
    }
  }

  .reviews,
  .projects{
    .swiper-scrollbar,
    .swiper-scrollbar-drag{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        height: 2em;
        width: 100%;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
    }
    .swiper-scrollbar{
      z-index: 3;
      display: block;
      height: 1px;
      border-radius: 0;
      background: #CFCDCD;
      &-drag{
        background-color: $primary;
      }
    }
  }

  .reviews{
    padding: 1.15em 0 1.3em;
    &__top{
      display: block;
    }
    &__info2{
      margin-top: 2em;
    }
    .swiper-scrollbar{
      margin-top: 2.25em;
    }
  }

  .reviews-slider{
    margin-top: 1.81em;
    padding: 0 .5em;
    .swiper{
      &-slide{
        width: 100%;
      }
      &-button{
        &-prev{
          left: -1.0625em;
        }
        &-next{
          right: -1.0625em;
        }
        &.swiper-button-disabled{
          opacity: 0;
        }
      }
    }
  }
}
