@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    height: 4.1875em;
    padding: 0 2.875em;
    border-radius: 0.5em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: $primaryHover;
    }
}

.phone-link{
    font-weight: 600;
    border-bottom: 1px dotted $primary;
    @include center;
    svg{
        width: 1.5em;
        height: auto;
        margin-right: .625em;
    }
    span{
        line-height: 201%;
    }
    &:hover{
        path{
            fill: $primary;
        }
    }
}

.swiper-button._outline,
.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    font-weight: 500;
    padding: 0;
    span{
        font-size: 0.875em;
        line-height: 111%;
    }
    svg{
        width: 1em;
        height: auto;
    }
    &.swiper-button-disabled{
        opacity: 1;
    }
    &:hover{
        border-color: $primary;
        background-color: $primary;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}

.swiper-button._outline{
    height: 3.375em;
    width: 3.375em;
    border-radius: 50%;
    background-color: #fff;
    @include center;
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.375em;
        padding: 0 1em;
    }
}
