.fg {
  position: relative;
  margin: 0;
  input {
    border-radius: 0.5em;
    border: 1px solid #F5F5F5;
    padding: 0 1em 0 3.5em;
    height: 4em;
    width: 100%;
    font-size: 1em;
    background-color: #F5F5F5;
    color: #000;
    font-weight: 300;
    &::placeholder {
      color: #696969;
    }
    &:focus {
      border-color: #C5C5C5;
      &~.fg__icon{
        path{
          fill: $primary;
        }
      }
    }
  }
  &__icon{
    left: 1.5em;
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
    width: 1em;
    height: auto;
  }
  &._error{
    input{
      border-color: $red;
    }
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: #F5F5F5;
        border-color: $primary;
        background-image: url(../img/check2.svg);
      }
    }
  }
  label {
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.125em;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
      border-radius: 0.5em;
      border: 1px solid #C5C5C5;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(.8125em);
    }
    a{
      text-decoration-line: underline;
      color: $primary;
      position: relative;
      z-index: 3;
      &:hover{
        text-decoration: none;
      }
    }
  }
  &._error{
    label{
      color: $red;
      &:before{
        border-color: $red;
      }
    }
  }
}

.rating{
  display: flex;
  flex-shrink: 0;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.125em;
    margin-right: .25em;
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: rgba($dark,.6);
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #F5BA2D;
      }
      &~.rating-input__item {
        path{
          fill: #F5BA2D;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: #F5BA2D;
  }
}


@media screen and (max-width: 576px){
  .fg{
    input{
      font-size: .875em;
      height: 3.42em;
      padding-left: 2.75em;
    }
    &__icon{
      left: .875em;
    }
  }

  .m-check{
    label{
      span{
        font-size: .875em;
      }
    }
  }
}
