.header{
  padding: 2.7em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 8.6875em;
    flex-shrink: 0;
    margin-right: 5.44em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    margin-left: auto;
  }
}

.menu{
  &__top{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 600;
    color: #676767;
    padding: .38em 0;
    border-bottom: 1px solid transparent;
    @include lastM(0 1.5em 0 0);
    span{
      font-size: 1.125em;
      letter-spacing: 0.0225em;
    }
    &._active{
      color: $black;
      border-color: $primary;
    }
  }
}

.header-contacts,
.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    background: #F8F8F8;
    &__inner{
      min-height: 5.5625em;
      justify-content: space-between;
      position: relative;
    }
    &__logo{
      width: 6.3125em;
      margin: 0;
      @include centerAbsolute;
    }
    &__phone{
      margin: 0;
      font-size: 1.125em;
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
  }

  .burger{
    width: 1.25em;
    height: 1.25em;
    display: flex;
    position: relative;
    img{
      @include centerAbsolute;
      width: 1.25em;
      transition: inherit;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    background-color: #fff;
    z-index: 10;
    overflow-y: auto;
    transform: translateX(-140%);
    transition: all .3s;
    &__top{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      height: 2.81em;
      border-bottom: 1px solid #D0D0D0;
      padding: 0 1em;
      margin-bottom: 2.87em;
    }
    &__close{
      width: .875em;
      img{
        width: 100%;
        display: block;
      }
    }
    &__content{
      padding: 0 1.25em 2.25em;
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      border: none;
      padding: 0;
      @include lastM(0 0 3.38em);
    }
  }

  .header-contacts{
    padding-top: 2.5em;
    margin-top: 3em;
    border-top: 1px solid #E0E0E0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__title{
      font-size: 1em;
      font-weight: 600;
      line-height: 140%;
      padding: 0 2em;
      span{
        color: $primary;
      }
    }
    &__phone{
      margin-top: 1.125em;
    }
    &__btn{
      width: 20em;
      max-width: 100%;
      margin: 1.5em auto 0;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__inner{
      padding: .875em 0;
      min-height: unset;
    }
    &__logo{
      position: static;
      transform: unset;
      width: 5em;
      order: -1;
    }
    &__phone{
      display: none;
    }
  }

  .burger{

  }
}
