.project-main{
  padding-bottom: 4.75em;
  .main__img{
    width: 23.125em;
    top: .69em;
  }
  .main__img2{
    width: 22.375em;
    top: .69em;
  }
  .info-block{
    max-width: 41em;
    &__top{
      max-width: 36em;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.props{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.875em;
  &__item{
    display: flex;
    color: $dark;
    @media screen and (min-width: 577px){
      &:not(:last-child){
        &:after{
          content: '';
          flex-shrink: 0;
          margin: 0 1em;
          background: #B9B9B9;
          width: 1.314em;
          height: 1px;
          transform: translateY(.375em) rotate(115.35deg);
        }
      }
    }
    span{
      font-size: 0.875em;
      font-weight: 400;
      line-height: 140%
    }
  }
}

.gallery{
  padding: 0 0 2.45em;
  &__grid{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.625em);
    margin-top: -1.875em;
  }
}

.gallery-item{
  width: calc(100% / 3 - 1.625em);
  margin: 1.875em 1.625em 0 0;
  &._extra-big{
    width: calc(100% - 1.625em);
    .gallery-item__img{
      padding-bottom: 41.37%;
    }
  }
  &._big{
    width: calc(50% - 1.625em);
    .gallery-item__img{
      padding-bottom: 57.14%;
    }
  }
  &__img{
    display: block;
    border-radius: .625em;
    @include imgCover(87.6%);
  }
}

.about{
  padding: 2.45em 0 3.38em;
  &__items{
    margin-top: 2.25em;
    @include grid100gap(2.25em);
  }
}

.about-group{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1em));
  gap: 2em;
  align-items: center;
  &:nth-child(even){
    .about-group{
      &__img{
        order: -1;
      }
      &__content{
        margin: 0 0 0 auto;
      }
    }
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  &__content{
    max-width: 31.8125em;
    color: #363636;
    font-weight: 400;
  }
}

.info{
  margin-top: 2.25em;
}

.offer{
  margin-top: 1.12em;
  padding: 2.25em;
  @include grid100gap(1.125em);
  border-radius: 0.9375em;
  border: 2px solid $primary;
  background: #FFF9F9;
  &__nav{
    display: flex;
  }
}


@media screen and (max-width: 992px){
  .project-main{
    padding: 5.5em 0 3.8em;
    .info-block{
      &__top{
        max-width: 36em;
        margin-left: 0;
      }
      .text53{
        font-size: 3em;
      }
      .text23{
        font-size: 1em;
        max-width: 25.25em;
      }
    }
    .main__img2{
      width: 16.625em;
      top: 2.76em;
    }
  }

  .props{
    margin-top: 1.125em;
    justify-content: unset;
  }

  .gallery{
    padding: 3.8em 0 1.4em;
    &__grid{
      width: calc(100% + 1em);
      margin-top: -1em;
    }
  }

  .gallery-item{
    width: calc(100% / 3 - 1em);
    margin: 1em 1em 0 0;
    &._extra-big{
      width: calc(100% - 1em);
    }
    &._big{
      width: calc(50% - 1em);
    }
  }

  .about{
    padding: 1.4em 0 1.12em;
    &__items{
      margin-top: 1.5em;
    }
  }

  .about-group{
    grid-template-columns: 100%;
    gap: 1.5em;
    &__img{
      border-radius: .625em;
      @include imgCover(56.7%);
    }
    &__content{
      max-width: 100%;
    }
  }

  .offer{
    .text32{
      font-size: 1.625em;
    }
  }
}


@media screen and (max-width: 576px){
  .project-main{
    padding: 6.5em 0 .22em;
    .info-block{
      max-width: 16.5em;
      &__top{
        max-width: 100%;
      }
      .text53{
        max-width: 100%;
        font-size: 1.875em;
        line-height: 111%;
      }
      .text23{
        font-size: .875em;
      }
    }
    .main__img2{
      top: -4.1em;
      width: 9.875em;
    }
  }

  .props{
    flex-direction: column;
    align-items: flex-start;
    &__item{
      @include lastM(0 0 .5em);
      span{
        font-size: .75em;
      }
    }
  }

  .gallery{
    padding: .22em 0 .82em;
    .container{
      padding: 0;
    }
    &__grid{
      margin: 0;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .08em));
      gap: .16em;
      width: 100%;
    }
  }

  .gallery-item{
    margin: 0 !important;
    width: 100% !important;
    &._extra-big{
      grid-column: 1/-1;
      .gallery-item__img{
        padding-bottom: 100.625%;
      }
    }
    &._big{
      .gallery-item__img{
        padding-bottom: 87.6%;
      }
    }
    &__img{
      border-radius: 0;
      padding-bottom: 87.6%;
    }
  }

  .about{
    padding: .8em 0;
    &__items{
      gap: 1.5em;
    }
  }

  .about-group{
    &__img{
      padding-bottom: 137.8%;
    }
  }

  .info{
    margin-top: 1.5em;
  }

  .offer{
    padding: 1.5em;
    margin-top: .75em;
    gap: .75em;
    .text32{
      font-size: 1.25em;
    }
    .text{
      p{
        font-size: .875em;
      }
    }
    &__btn{
      width: 100%;
      padding: 0;
      height: 3.875em;
      span{
        font-size: .875em;
      }
    }
  }
}
