.text73{
  font-size: 4.5625em;
  line-height: 111%;
}

.text53{
  font-size: 3.3125em;
  line-height: 131%;
}

.text50{
  font-size: 3.125em;
  line-height: 111%;
}

.text32{
  font-size: 2em;
  line-height: 111%;
}

.text26{
  font-size: 1.625em;
  line-height: 140%;
}

.text23{
  font-size: 1.4375em;
  line-height: 140%;
}

.text h4,
.text22{
  font-size: 1.375em;
  line-height: 120%;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 140%;
}

.text p,
.text16{
  font-size: 1em;
  line-height: 140%;
}

.text li,
.text14{
  font-size: .875em;
  line-height: 140%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.primary-text{
  color: $primary;
}

.text{
  @include grid100gap(1.125em);
  h3{
    font-size: 2.375em;
    font-weight: 600;
    line-height: 111%;
    margin-bottom: .5em;
  }
  h4{
    font-weight: 600;
    line-height: 160%;
    letter-spacing: 0.01375em;
  }
  p{
    line-height: 160%;
    letter-spacing: 0.01em;
  }
  li{
    line-height: 160%;
    letter-spacing: 0.00875em;
    position: relative;
  }
  ol,ul{
    @include grid100gap(.625em);
    li{
      &:before{
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
    }
  }
  ul{
    li{
      padding-left: 1.28em;
      &:before{
        content: '';
        border: 2px solid $primary;
        width: .6em;
        height: .6em;
      }
    }
  }
  ol{
    counter-reset: list;
    li{
      padding-left: 1.64em;
      counter-increment: list;
      &:before{
        content: counter(list) '. ';
        font-weight: 700;
        color: $primary;
        @include sameNumWidth;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text73{
    font-size: 4em;
  }

  .text38-tablet{
    font-size: 2.375em;
  }

  .text53{
    font-size: 2.0625em;
  }

  .text24-tablet{
    font-size: 1.5em;
  }

  .text23{
    font-size: 1.125em;
    line-height: 150%;
  }

  .text22-tablet{
    font-size: 1.375em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text{
    h3{
      font-size: 2.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .text73{
    font-size: 2.75em;
  }

  .text53{
    font-size: 1.25em;
  }

  .text50{
    font-size: 2.125em;
  }

  .text24-mob{
    font-size: 1.5em;
  }

  .text16-mob,
  .text23{
    font-size: 1em;
  }

  .text22-mob{
    font-size: 1.375em;
  }

  .text20-mob{
    font-size: 1.25em;
    line-height: 130%;
  }

  .text14-mob{
    font-size: .875em;
    line-height: 140%;
  }

  .text{
    h3{
      font-size: 1.625em;
    }
  }
}
